import React from 'react';
import { useTranslation } from 'react-i18next'; // Localization tool

import AppConfigContext from '@context/AppConfigContext'; // App config context
import { CONTACTS_EMAIL } from '@utils/constants'; // Default contacts email
import Logo from '@componentsLayout/Logo/Logo'; // Logo
import FooterMenu from './FooterMenu/FooterMenu'; // Footer menu

/**
 * Footer
 * Footer menu placement is changing via mobile detext
 */
function Footer() {
  const { isMobile } = React.useContext(AppConfigContext);
  const { t } = useTranslation(['footer']);

  return (
    <footer className="footer">
      <div className="footer__inner">
        <div className="footer__inner-row">
          <Logo isDark={true} />
          {!isMobile && <FooterMenu />}
        </div>
        <a href={CONTACTS_EMAIL} className="footer__link">{ t('footer:contactButtonText', 'Contact Us') }</a>
      </div>
      {isMobile && <FooterMenu />}
    </footer>
  );
}

export default Footer;
