import React from 'react';
import Collapse from 'react-bootstrap/Collapse';
import ListGroup from 'react-bootstrap/ListGroup';

import AppConfigContext from '@context/AppConfigContext'; // Main app config context

// @todo: wait for faq realization on backend
import { mainPageFaqList } from '@utils/staticContent'; // Static faq list, @todo: delete later

function FaqList() {
  const { isMobile } = React.useContext(AppConfigContext);
  const [openedList, setOpenedList] = React.useState([]);

  return (
    <div className="faq-list">
      <ListGroup className="faq-list__inner">
        {
          mainPageFaqList?.map((item, key) => <ListGroup.Item key={`faq-${key}`}>
            <p className="faq-list__title" onClick={() => { setOpenedList([...openedList, key]); }}>
              <span>{item.title}</span>
              {isMobile && !openedList.includes(key) && <span className="faq-list__arrow" />}
            </p>
            {!isMobile && <div className="faq-list__text" dangerouslySetInnerHTML={{ __html: item.text }} />}
            {isMobile
            && <Collapse in={openedList.includes(key)}>
              <div className="faq-list__text" dangerouslySetInnerHTML={{ __html: item.text }} />
            </Collapse>}
          </ListGroup.Item>)
        }
      </ListGroup>
    </div>
  );
}

export default FaqList;
