export const types = {
  INIT_NEW_SCAN: 'INIT_NEW_SCAN',
  INIT_NEW_SCAN_SUCCESS: 'INIT_NEW_SCAN_SUCCESS',
  INIT_NEW_SCAN_ERROR: 'INIT_NEW_SCAN_ERROR',

  REGISTER_USER: 'REGISTER_USER',
  REGISTER_USER_SUCCESS: 'REGISTER_USER_SUCCESS',
  REGISTER_USER_ERROR: 'REGISTER_USER_ERROR',
};

export const initNewScan = (values, callback) => ({
  type: types.INIT_NEW_SCAN,
  values,
  callback,
});

export const registerUser = (values, callback) => ({
  type: types.REGISTER_USER,
  values,
  callback,
});
