import React from 'react';
import Form from 'react-bootstrap/Form';

import AppConfigContext from '@context/AppConfigContext'; // Main app config context
import InputGroup from 'react-bootstrap/InputGroup';

/**
 * Auth field for registration/login/password restoration
 * @param {Object} props
 * @returns
 */
function AuthField({
  name,
  value,
  type,
  placeholder,
  hasError,
  errorText,
  isValid,
  handleChange,
  helper,
}) {
  const isPassword = type === 'password';
  const [inputType, setInputType] = React.useState(type);
  const { isMobile } = React.useContext(AppConfigContext);

  /**
   * Change input type handler for password
   * @param {Object} e
   */
  const handlePasswordTypeChange = (e) => {
    const eventType = e.type;
    if (!eventType || (eventType === 'click' && !isMobile) || (eventType !== 'click' && isMobile)) {
      return;
    }

    let newType = '';
    switch (eventType) {
      case 'mouseleave':
        newType = 'password';
        break;
      case 'mouseenter':
        newType = 'text';
        break;
      default:
        newType = (inputType === 'text') ? 'password' : 'text';
        break;
    }
    setInputType(newType);
  };

  return (
    <InputGroup>
      <Form.Floating className={`${isValid ? 'form-floating--valid' : ''} ${hasError ? 'form-floating--error' : ''}`}>
        <Form.Control
          type={inputType}
          className={`${hasError ? 'form-control--error' : ''}`}
          name={name}
          placeholder={placeholder}
          value={value}
          onChange={handleChange}
        />
        <Form.Label>{placeholder}</Form.Label>
        {isPassword && !hasError && value && !isValid
          && <span
            className={`form-floating__password-icon${inputType === 'text' ? ' form-floating__password-icon--visible' : ''}`}
            onMouseEnter={handlePasswordTypeChange}
            onMouseLeave={handlePasswordTypeChange}
            onClick={handlePasswordTypeChange}
          />}
      </Form.Floating>
      {hasError && <Form.Control.Feedback type="invalid">{errorText}</Form.Control.Feedback>}
      {helper && <Form.Text>{helper}</Form.Text>}
    </InputGroup>
  );
}

export default AuthField;
