import { call, put, takeEvery } from 'redux-saga/effects';
import axios from 'axios';
import { types } from '@actions';

import { API_DOMAIN, API_ENDPOINTS } from '@utils/constants.js'; // Api endpoints app config

// WORKERS
/**
 * Init new scan request
 * @param {Object} params
 */
export function* workInitScan({ data, callback }) {
  try {
    const uri = `${API_DOMAIN}${API_ENDPOINTS.SCAN}`;
    const response = yield call(axios.post, uri, data);

    callback();

    // @todo: process error from response on backend ready
    yield put({
      type: types.INIT_NEW_SCAN_SUCCESS,
      payload: response.data,
    });
  } catch (error) {
    console.log('Request failed! Could not init scan.');
  }
}

// WATCHERS
/**
 * Init new scan watcher
 */
export function* watchInitScan() {
  yield takeEvery(types.INIT_NEW_SCAN, workInitScan);
}
