import React, { Suspense } from 'react';
import Modal from 'react-bootstrap/Modal';
import { useTranslation } from 'react-i18next'; // Localization tool

import AppConfigContext from '@context/AppConfigContext'; // Main app config context
import Register from './Register'; // Register component

/**
 * Register bootstrap modal wrap
 * @param {Object} props
 * @todo: i18n mixin
 */
function RegisterModal({ isShown, closeHandler, openLoginHandler }) {
  const { t } = useTranslation(['register']);
  const { isMobile } = React.useContext(AppConfigContext);
  const handleClose = () => closeHandler(false);

  /**
   * Open register modal
   */
  const openLogin = () => {
    closeHandler(false);
    openLoginHandler(true);
  };

  return (
    <Modal show={isShown} onHide={handleClose} fullscreen={isMobile} centered>
      <Modal.Header closeButton>
        <Modal.Title>{t('joinPhpSecure', 'Join PhpSecure')}</Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <Suspense fallback={false}>
          <Register openLogin={openLogin} />
        </Suspense>
      </Modal.Body>
    </Modal>
  );
}

export default RegisterModal;
