export const mainBannerText = `function getUserField($field) {
    global $conn;
    return $conn->getColumn("SELECT *, mysqli_real_escape_string($conn, $field) FROM members Where USERID = 1");
  }\n
  $user = getUserField($_GET["field"]);\n
  $user = getUserField($_POST["field"]);\n
  $user = getUserField($_REQUEST["field"]);\n
  $user = getUserField("username");`;

