import React from 'react';
import { connect } from 'react-redux';

import Logo from '@componentsLayout/Logo/Logo'; // Logotype component, different types
import HeaderMenu from '@componentsLayout/Header/HeaderMenu/HeaderMenu'; // Header menu, changes on loggedIn state
import RegisterModal from '@componentsAuth/RegisterModal'; // Register modal component
import LoginModal from '@componentsAuth/LoginModal'; // Login modal component
import AppConfigContext from '@context/AppConfigContext'; // Main context with app config

/**
 * Site header
 * @param {Object} props
 */
function Header({ isFixed, authData }) {
  const { isMobile } = React.useContext(AppConfigContext);
  const headerClass = isFixed ? 'header header_state_fixed' : 'header';
  const isLoggedIn = authData?.isLoggedIn;

  const [isRegisterOpened, setIsRegisterOpened] = React.useState(false);
  const [isLoginOpened, setIsLoginOpened] = React.useState(false);

  return (
    <header className={headerClass}>
      <div className="header__inner">
        <Logo />
        {!isMobile
          && <HeaderMenu
            openRegisterHandler={setIsRegisterOpened}
            openLoginHandler={setIsLoginOpened}
          />}

        {!isLoggedIn && isMobile
          && <button type="button" className="header__inner-mobile-btn" onClick={setIsRegisterOpened} />}

        {!isLoggedIn
          && <RegisterModal
            isShown={isRegisterOpened}
            closeHandler={setIsRegisterOpened}
            openLoginHandler={setIsLoginOpened}
          />}

        {!isLoggedIn
          && <LoginModal
            isShown={isLoginOpened}
            closeHandler={setIsLoginOpened}
            openRegisterHandler={setIsRegisterOpened}
          />}
      </div>
    </header>
  );
}

const mapStateToProps = (state) => ({
  authData: state.auth,
});

export default connect(mapStateToProps)(Header);
