import React from 'react';
import { connect } from 'react-redux';
import { Alert } from 'react-bootstrap';
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';
import * as yup from 'yup';
import YupPassword from 'yup-password';
import { Formik } from 'formik';
import { useTranslation } from 'react-i18next'; // Localization tool

import AuthField from './AuthField'; // Поле для компонентов аутентификации

YupPassword(yup);

/**
 * Register components, usage in modal/register-page
 * @param {Object} props
 */
function Register({ authData, openLogin }) {
  const { t } = useTranslation(['register']);
  const formikRef = React.useRef();
  const [isTriedToContinue, setIsTriedToContinue] = React.useState(false);
  const [isFirstStep, setIsFirstStep] = React.useState(true);

  /**
   * Register user submit handler
   * @param {Object} data
   */
  const submitHandler = (data) => {
    console.log(data);
  };

  const regexp = {
    availableUserNameRegexp: /^[a-zA-Z0-9-_]+$/g,
    unAvailableUserNameRegexp: /[^a-zA-Z0-9-_]+/g,
  };
  const passwordContentError = t('register:passwordNotValid', 'Password must be min. 8 characters. Combine numbers, upper and lowercase letters.');
  const validationSchema = yup.object().shape({
    email: yup.string()
      .required(t('register:emailIsRequired', 'Email is required'))
      .email(t('register:emailNotValid', 'Email is not valid')),
    username: yup.string()
      .required(t('register:enterUsername', 'Enter a username'))
      .matches(regexp.availableUserNameRegexp, (data) => {
        let matches = data.value.match(regexp.unAvailableUserNameRegexp);
        if (!matches || matches.length === 0) {
          return false;
        }

        matches = matches.map((match) => {
          if (match.trim() === '') {
            return 'space';
          }

          return match.trim();
        });

        return t('register:removeCharacters', { characterList: matches.join(', ') });
      })
      .min(6, t('register:minUsernameLengthError', 'That’s too short. A great username must include at least 6 characters.')),
    password: yup.string()
      .required(t('register:enterPassword', 'Enter a password'))
      .min(8, passwordContentError)
      .minLowercase(1, passwordContentError)
      .minUppercase(1, passwordContentError)
      .minNumbers(1, passwordContentError)
      .test(
        'password-match-login',
        t('register:notSecurePassword', 'Your password is not secure enough. Please choose a different password.'),
        function (value) {
          return value !== this.parent.username;
        },
      ),
  });

  /**
   * Step change handler
   */
  const goToSecondStep = () => {
    const formikData = formikRef.current;
    if (!formikData) {
      return;
    }

    setIsTriedToContinue(true);
    formikData.validateField('email');
    if (formikData.errors.email || !formikData.values.email) {
      return;
    }

    setIsFirstStep(false);
  };

  return (
    <>
      {authData?.isNeedShowLimitNotify && isFirstStep && <Alert>{t('register:freeScanAlert')}</Alert>}
      <Formik
        innerRef={formikRef}
        validateOnBlur={true}
        initialValues={{ email: '', username: '', password: '' }}
        onSubmit={submitHandler}
        validationSchema={validationSchema}
      >
        {({
          handleSubmit,
          handleChange,
          values,
          errors,
          touched,
        }) => (
          <Form
            noValidate
            onSubmit={handleSubmit}
          >
            <div className={isFirstStep ? '' : 'd-none'}>
              <AuthField
                name="email"
                value={values.email}
                type="text"
                placeholder={t('register:email', 'E-mail')}
                hasError={isTriedToContinue && errors.email}
                errorText={errors.email}
                isValid={values.email && !errors.email}
                handleChange={handleChange}
              />
              <Button type="button" className="btn btn-primary border-0 w-100" onClick={goToSecondStep}>{t('register:continue', 'Continue')}</Button>
            </div>
            <div className={isFirstStep ? 'd-none' : ''}>
              <AuthField
                name="username"
                value={values.username}
                type="text"
                placeholder={t('register:chooseUsername', 'Choose a username')}
                hasError={errors.username && touched.username}
                errorText={errors.username}
                isValid={values.username && !errors.username}
                handleChange={handleChange}
              />
              <AuthField
                name="password"
                value={values.password}
                type="password"
                placeholder={t('register:choosePassword', 'Choose a Password')}
                hasError={errors.password && touched.password}
                errorText={errors.password}
                isValid={values.password && !errors.password}
                handleChange={handleChange}
                helper={t('register:passwordHelper', '8 characters or longer. Combine upper and lowercase letters and numbers')}
              />
              <Button type="submit" className="btn btn-primary border-0 w-100">{t('register:join', 'Join')}</Button>
              <div className="modal__footer-text modal__footer-text--small modal__footer-text--centered" dangerouslySetInnerHTML={{ __html: t('register:confirmTermsText', { openTag: '<a href="/terms" target="_blank" class="modal__footer-text-link">', closeTag: '</a>' }) }} />
            </div>
            <div className="modal__footer-text">{t('register:haveAccount', 'Already have an account?')} <span className="modal__footer-text-link" onClick={openLogin}>{t('register:signIn', 'Sign In')}</span></div>
          </Form>
        )}
      </Formik>
    </>
  );
}

const mapStateToProps = (state) => ({
  authData: state.auth,
});

export default connect(mapStateToProps)(Register);
