import React from 'react';
import { useTranslation } from 'react-i18next'; // Localization tool

/**
 * Site header menu
 * @todo: for i18n
 * @param {Object} props
 */
function HeaderMenu({ openRegisterHandler, openLoginHandler }) {
  const { t } = useTranslation(['header']);
  const openRegister = () => openRegisterHandler(true);
  const openLogin = () => openLoginHandler(true);

  return (
        <div className="header-menu">
            <button type="button" className="btn btn-outline-light me-3" onClick={openLogin}>{ t('header:logIn', 'Log In') }</button>
            <button type="button" className="btn btn-outline-primary" onClick={openRegister}>{ t('header:createAccount', 'Create Account') }</button>
        </div>
  );
}

export default HeaderMenu;
