import React from 'react';
import {
  Route,
  Routes,
  useLocation,
} from 'react-router-dom';
import lodash from 'lodash';

/**
 * Base layout components
 */
import Header from '@componentsLayout/Header/Header'; // Header
import Footer from '@componentsLayout/Footer/Footer'; // Footer

/**
 * Pages
 */
import Main from '@pages/Main/Main'; // Main page

import { PAGES, DEFAULT_LANG } from '@utils/constants'; // URI page config, default language
import { isMobile } from '@utils/helpers'; // Helper of mobile detection via device width @todo: add isTablet check
import AppConfigContext from '@context/AppConfigContext'; // Main app config context

import './App.style.scss'; // Overall styles of SPA

/**
 * Main app component
 */
function App() {
  const location = useLocation();
  const defaultConfig = { lang: DEFAULT_LANG, isMobile: isMobile() };
  const [appConfig, setAppConfig] = React.useState(defaultConfig);
  const [isMainPage, setIsMainPage] = React.useState(location.pathname === PAGES.MAIN);

  React.useEffect(() => {
    // detect isMobile to config context
    window.addEventListener('resize', lodash.throttle(() => {
      setAppConfig({ ...appConfig, ...{ isMobile: isMobile() } });
    }, 200));
  }, []);

  // Change navigation hook
  React.useEffect(
    () => {
      setIsMainPage(location.pathname === PAGES.MAIN);
    },
    [location],
  );

  return (
    <AppConfigContext.Provider value={appConfig}>
      <div className="position-relative">
        <Header isFixed={isMainPage} />
        <main>
          <Routes>
            <Route path="/test123456/" element={<Main />} />
          </Routes>
        </main>
        <Footer />
      </div>
    </AppConfigContext.Provider>
  );
}

export default App;
