import React from 'react';
import { APP_NAME } from '@utils/constants';
import './Logo.scss';

/**
 * Logo with two states - light (default) / dark
 * @param {Object} props
 */
function Logo({ isDark }) {
  const logoClass = isDark ? 'logo logo--dark' : 'logo';

  return (
    <a href="/" className={logoClass} alt={APP_NAME}></a>
  );
}

export default Logo;
