import React, { Suspense } from 'react';
import Modal from 'react-bootstrap/Modal';
import { useTranslation } from 'react-i18next'; // Localization tool

import AppConfigContext from '@context/AppConfigContext'; // Main app config context
import Login from './Login'; // Login component

/**
 * Register bootstrap modal wrap
 * @param {Object} props
 * @todo: i18n mixin
 */
function LoginModal({ isShown, closeHandler, openRegisterHandler }) {
  const { t } = useTranslation(['login']);
  const { isMobile } = React.useContext(AppConfigContext);
  const handleClose = () => closeHandler(false);

  /**
   * Open register modal
   */
  const openRegister = () => {
    closeHandler(false);
    openRegisterHandler(true);
  };

  return (
    <Modal show={isShown} onHide={handleClose} fullscreen={isMobile} centered>
      <Modal.Header closeButton>
        <Modal.Title>{t('login:signIn', 'Sign In')}</Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <Suspense fallback={false}>
          <Login openRegister={openRegister} />
        </Suspense>
      </Modal.Body>
    </Modal>
  );
}

export default LoginModal;
