import React from 'react';
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';
import { Formik } from 'formik';
import * as yup from 'yup';
import { useTranslation } from 'react-i18next'; // Localization tool

import AuthField from './AuthField'; // Поле для компонентов аутентификации

/**
 * Login component
 * @param {Object} props
 * @returns
 */
function Login({ openRegister }) {
  const { t } = useTranslation(['login']);

  /**
   * Register user submit handler
   * @param {Object} data
   */
  const submitHandler = (data) => {
    console.log(data);
  };

  const validationSchema = yup.object().shape({
    login: yup.string()
      .required(t('login:enterLogin', 'Enter login')),
    password: yup.string()
      .required(t('login:enterPassword', 'Enter a password')),
  });

  return (
    <Formik
      validateOnBlur={true}
      initialValues={{ login: '', password: '' }}
      onSubmit={submitHandler}
      validationSchema={validationSchema}
    >
      {({
        handleSubmit,
        handleChange,
        values,
        errors,
        touched,
      }) => (
        <Form
          noValidate
          onSubmit={handleSubmit}
        >
          <AuthField
            name="login"
            value={values.login}
            type="text"
            placeholder={t('login:loginPlaceholder', 'Email address or username')}
            hasError={errors.login && touched.login}
            errorText={errors.login}
            handleChange={handleChange}
          />
          <AuthField
            name="password"
            value={values.password}
            type="password"
            placeholder={t('login:password', 'Password')}
            hasError={errors.password && touched.password}
            errorText={errors.password}
            handleChange={handleChange}
          />
          <Form.Group controlId="rememberMeCheckbox">
            <Form.Check type="checkbox" label={t('login:rememberMe', 'Remember me')} />
          </Form.Group>
          <Button type="submit" className="btn btn-primary border-0 w-100">{t('login:signIn', 'Sign In')}</Button>
          <div className="modal__footer-text">{t('login:newToPhpSecure', 'New to PhpSecure?')} <span className="modal__footer-text-link" onClick={openRegister}>{t('login:joinNow', 'Join now')}</span></div>
        </Form>
      )}
    </Formik>
  );
}

export default Login;
