import React from 'react';
import { connect } from 'react-redux';
import Form from 'react-bootstrap/Form';
import InputGroup from 'react-bootstrap/InputGroup';
import Button from 'react-bootstrap/Button';
import * as yup from 'yup';
import { Formik } from 'formik';
import { useTranslation } from 'react-i18next'; // Localization tool

import AppConfigContext from '@context/AppConfigContext'; // Main context with app config
import { initNewScan } from '@actions'; // New scan init action

/**
 * New scan form with project name
 * @todo: i18n, submit callback
 */
function CodeScanForm(props) {
  const { isMobile } = React.useContext(AppConfigContext);
  const { t } = useTranslation(['scan']);
  const regexp = {
    availableNameRegexp: /^[a-zA-Z0-9-_$?/\\.,!*@:%._+~#=&() ]+$/g,
    unAvailableNameRegexp: /[^a-zA-Z0-9-_$?/\\.,!*@:%._+~#=&() ]+/g,
  };
  const validationSchema = yup.object().shape({
    projectName: yup.string()
      .required(t('scan:enterSiteName', 'Please enter site or script name, 60 max.'))
      .max(60, t('scan:maxLengthError', 'Maximum 60 characters'))
      .matches(regexp.availableNameRegexp, (data) => {
        const matches = data.value.match(regexp.unAvailableNameRegexp);
        if (!matches || matches.length === 0) {
          return false;
        }

        return t('scan:removeCharacters', { characterList: matches.join(', ') });
      }),
  });

  /**
   * New scan create handler (on form submit)
   * @todo: open modal, add data from response to store
   * @param {Object} data
   */
  const submitHandler = (data) => {
    props.initNewScan(data, (res) => console.log(res));
  };

  return (
    <Formik
      initialValues={{ projectName: '' }}
      onSubmit={submitHandler}
      validationSchema={validationSchema}
    >
      {({
        handleSubmit,
        handleChange,
        values,
        errors,
        isSubmitting,
      }) => (
        <Form
          noValidate
          className="code-scan-form"
          onSubmit={handleSubmit}
        >
          <InputGroup className={`input-group--dark input-group--rounded ${(errors.projectName) ? 'input-group--error' : ''}`}>
            <span className="code-scan-form__icon" />
            <Form.Floating>
              <Form.Control
                type="text"
                id="code-scan-input"
                name="projectName"
                className="form-control--theme-dark form-control--rounded"
                placeholder={ t('scan:websiteOrScriptName', 'Website or script name') }
                value={values.projectName}
                onChange={handleChange}
              />
              <Form.Label htmlFor="code-scan-input">{ t('scan:websiteOrScriptName', 'Website or script name') }</Form.Label>
            </Form.Floating>
            {!isMobile && <Button type="submit" className="btn btn-primary btn-primary--no-shadow btn-primary--rounded border-0" disabled={isSubmitting}>{ t('scan:scanForFree', 'Scan For Free!') }</Button>}
          </InputGroup>
          {!!errors.projectName && <Form.Control.Feedback type="invalid" className="invalid-feedback--scan-form">{errors.projectName}</Form.Control.Feedback>}
          {isMobile && <Button type="submit" className="btn btn-primary btn-primary--no-shadow btn-primary--rounded border-0 w-100" disabled={isSubmitting}>{ t('scan:scanForFree', 'Scan For Free!') }</Button>}
        </Form>
      )}
    </Formik>
  );
}

const mapStateToProps = (state) => ({
  scan: state.scan,
});

export default connect(mapStateToProps, { initNewScan })(CodeScanForm);
